	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-08-a-conversation-with-ross-levinsohn/index.md absPath of file >>> MarkdownRemark","html":"<p>Arranged the deal and bought Myspace. Youtube is their biggest strategic partner. On the Internet may of your biggest competitors are also your partners. Google will power search on the news corp sites. They get to sell advertising, and news corp gets 900 million. Myspace cost 580 million. Not a bad deal.\nHowever, he also said about Myspace, “I don’t sleep, it’s why I put on 30 pounds over the last year.” These businesses take work. Spent the last nine months building out software and hardware.\nAfter we bought the site we spent a lot of time on safety.</p>\n<p>What do you look for in a company when you do an acquisition? It is easy to start a business today. It starts with the enthusiasm of the entrepreneur.</p>\n<p>Why doesn’t Myspace open their APIs? They are thinking of it. Talk to Tom. Lot’s of people bring this up. Dan Gould (My Space employee) likes the idea.</p>","frontmatter":{"title":"A Conversation with Ross Levinsohn","date":"November 08, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/a-conversation-with-ross-levinsohn"}}

